import zySearch from '../../components/zy-search/zy-search.vue'
import QQMapWX from '../../js_sdk/qqmap-wx-jssdk1.2/qqmap-wx-jssdk.js';
import {
	mapState
} from 'vuex';
const qqmapsdk = new QQMapWX({
	key: 'XSWBZ-MHZ3K-U76JO-AU4NT-WKNYK-B2BA4'
});
export default {
	components: {
		zySearch
	},
	data() {
		return {
			searchText: '',
			historyList: uni.getStorageSync('search_cache'),
			wantList: ['美食', '住宿', '休闲', '娱乐', '美食', '住宿', '休闲', ]
		}
	},
	computed: {
		...mapState({
			location: state => state.UserLocation
		}),
	},
	mounted() {

	},
	methods: {
		searchStart() {
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else {
				this.setSerachStorage();
				console.log(this.location)
				qqmapsdk.search({
					keyword: this.searchText,
					location: {
						latitude: this.location.user_location.latitude,
						longitude: this.location.user_location.longitude
					},
					success: function(res) {
						console.log(res);
					},
					fail: function(res) {
						console.log(res);
					}
				});
			}
		},
		setSerachStorage() {
			let _this = this;
			uni.getStorage({
				key: 'search_cache',
				success(res) {
					let list = res.data;
					console.log(list);
					if (list.length > 9) {
						for (let item of list) {
							if (item == _this.searchText) {
								return false;
							}
						}
						list.pop();
						list.unshift(_this.searchText);
					} else {
						for (let item of list) {
							if (item == _this.searchText) {
								return false;
							}
						}
						list.unshift(_this.searchText);
					}
					_this.historyList = list;
					uni.setStorage({
						key: 'search_cache',
						data: _this.historyList
					});
				},
				fail() {
					_this.historyList = [];
					_this.historyList.push(_this.searchText);
					uni.setStorage({
						key: 'search_cache',
						data: _this.historyList
					});
				}
			})
		}
	},
}
